"use client";

import { useState } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Loader2, Download, AlertCircle } from "lucide-react";

interface SpecificationProps {
  analysis: any;
  onGenerated?: (spec: string) => void;
}

export function Specification({ analysis, onGenerated }: SpecificationProps) {
  const [spec, setSpec] = useState<string>("");
  const [isGenerating, setIsGenerating] = useState(false);
  const [error, setError] = useState<string>("");

  const generateSpec = async () => {
    setIsGenerating(true);
    setError("");
    let fullSpec = "";

    try {
      const response = await fetch("/api/bedrock/spec", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ analysis }),
      });

      if (!response.ok) {
        throw new Error("Î™ÖÏÑ∏ÏÑú ÏÉùÏÑ± Ïã§Ìå®");
      }

      const reader = response.body?.getReader();
      const decoder = new TextDecoder();

      if (reader) {
        while (true) {
          const { done, value } = await reader.read();
          if (done) break;

          const chunk = decoder.decode(value);
          const lines = chunk.split("\n");

          for (const line of lines) {
            if (line.startsWith("data: ")) {
              const data = line.slice(6);
              if (data === "[DONE]") {
                setSpec(fullSpec);
                onGenerated?.(fullSpec);
                setIsGenerating(false);
                return;
              }
              try {
                const parsed = JSON.parse(data);
                fullSpec += parsed.text;
                setSpec(fullSpec);
              } catch (e) {
                // Ignore parse errors
              }
            }
          }
        }
      }
    } catch (err) {
      console.error("Error generating spec:", err);
      setError(err instanceof Error ? err.message : "Î™ÖÏÑ∏ÏÑú ÏÉùÏÑ± Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§");
      setIsGenerating(false);
    }
  };

  const downloadSpec = () => {
    const blob = new Blob([spec], { type: "text/markdown" });
    const url = URL.createObjectURL(blob);
    const a = document.createElement("a");
    a.href = url;
    a.download = `agent-spec-${new Date().toISOString().slice(0, 10)}.md`;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  };

  return (
    <Card>
      <CardHeader>
        <CardTitle>üìÑ Íµ¨ÌòÑ Î™ÖÏÑ∏ÏÑú</CardTitle>
      </CardHeader>
      <CardContent className="space-y-4">
        {error && (
          <div className="bg-red-50 dark:bg-red-950 p-3 rounded-lg flex items-start gap-2">
            <AlertCircle className="h-5 w-5 text-red-600 dark:text-red-400 mt-0.5" />
            <p className="text-sm text-red-600 dark:text-red-400">{error}</p>
          </div>
        )}
        
        {!spec || isGenerating ? (
          <Button
            onClick={generateSpec}
            disabled={isGenerating}
            className="w-full"
          >
            {isGenerating ? (
              <>
                <Loader2 className="h-4 w-4 animate-spin mr-2" />
                Î™ÖÏÑ∏ÏÑú ÏÉùÏÑ± Ï§ë...
              </>
            ) : (
              "ü§ñ ClaudeÎ°ú ÏÉÅÏÑ∏ Î™ÖÏÑ∏ÏÑú ÏÉùÏÑ±"
            )}
          </Button>
        ) : null}
        
        {spec && (
          <>
            {!isGenerating && (
              <Button onClick={downloadSpec} className="w-full">
                <Download className="h-4 w-4 mr-2" />
                Î™ÖÏÑ∏ÏÑú Îã§Ïö¥Î°úÎìú (Markdown)
              </Button>
            )}
            <div className="border rounded-lg p-4 max-h-[600px] overflow-y-auto bg-muted/30">
              <pre className="text-sm whitespace-pre-wrap font-mono">{spec}</pre>
              {isGenerating && (
                <span className="inline-block w-2 h-4 bg-foreground animate-pulse ml-1" />
              )}
            </div>
          </>
        )}
      </CardContent>
    </Card>
  );
}
